import React, {useState} from 'react'
import s from './header.module.css'
import cn from "classnames";
import {NavLink} from 'react-router-dom'
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faBars} from '@fortawesome/free-solid-svg-icons'

export const Header = ({page}:{page:string}) => {

    const [active, setActive] = useState(false)
    const [shakeCompany, setShakeCompany] = useState(false);
    const [shakeWork, setShakeWork] = useState(false);
    const [shakeCareers, setShakeCareers] = useState(false);
    const [shakeContact, setShakeContact] = useState(false);

    const animate = (set: any) => {

        set(true);
        setTimeout(() => set(false), 1000);
    }

    const handleEvent = (e:any) => e.target.classList.remove(s.current);

    return <div className={s.navbar}>
        <header className={cn({[s.active]: active}, s.w_container, s.header)}>
            <div className={s.up_nav}>
            <a href="/" className={[s.logo, s.brand].join(" ")}><img alt={"temy-logo-simple"} className={s.logo_img}
                                                src={"https://assets.website-files.com/5d2593d4e31f05ed4edd50f5/5dbc29afc0a72841e8d7e045_temy-logo-simple.svg"}/></a>
            <div onClick={() => setActive(!active)} className={cn({[s.active]: active}, s.menu_toggle)}><FontAwesomeIcon
                icon={faBars}/></div>
            </div>
            <nav className={cn({[s.active]: active})}>
                <NavLink onMouseEnter={handleEvent} onClick={() => animate(setShakeCompany)}
                         className={[shakeCompany && "shake", s.link, s.company, page === 'company' && s.current].join(" ")} to="/">Company</NavLink>
                <NavLink onMouseEnter={handleEvent}  onClick={() => animate(setShakeWork)}
                         className={[shakeWork && "shake", s.link, s.work, page === 'work' && s.current].join(" ")} to="/">Work</NavLink>
                <NavLink onMouseEnter={handleEvent}  onClick={() => animate(setShakeCareers)}
                         className={[shakeCareers && "shake", s.link, s.careers, page === 'careers' && s.current].join(" ")} to="/">Careers</NavLink>
                <NavLink onMouseEnter={handleEvent} onClick={() => animate(setShakeContact)}
                         className={[shakeContact && "shake", s.link, s.work, s.contact_button, page === 'contact' && s.current].join(" ")}
                         to="/contact">Contact us</NavLink>
            </nav>
            <div className={s.clearfix}/>
        </header>
    </div>
}